import { useEffect } from 'react';
import {
    IconButton,
    Button,
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    useDisclosure,
    Text
  } from '@chakra-ui/react';
import { AiOutlineQuestionCircle } from 'react-icons/ai';

export const TutorialButton = (props) => {
    const { visited } = props;
    const { isOpen, onOpen, onClose } = useDisclosure();

    // 
    useEffect(() => {
    if (!visited) {
        onOpen();
    }
    }, [visited, onOpen]);
    
    return (
        <>
            <IconButton icon={<AiOutlineQuestionCircle />}  onClick={onOpen}/>
            <Modal size={'xl'} isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                <ModalHeader>AI </ModalHeader>
                <ModalCloseButton />
                <ModalBody>
                    <Text>AIChatGPT </Text>
                    <Text>AI</Text>
                    <Text></Text>
                </ModalBody>
                <ModalFooter>
                    <Button colorScheme='blue' mr={3} onClick={onClose}>
                    
                    </Button>
                </ModalFooter>
                </ModalContent>
            </Modal>
        </>
    );
};